<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="内容" style="width:80%" :required="true">
            <el-input
              type="textarea"
              :autosize="{ minRows: 5, maxRows: 20 }"
              :maxlength="200"
              :show-word-limit="true"
              v-model="requestParam.msg"
              :clearable="true"
              class="inputstyle"
            ></el-input>
          </el-form-item>
          <el-form-item label="间隔(s)" style="width:80%">
            <el-input placeholder="测试专用" v-model="timeDet"> </el-input>
          </el-form-item>
          <el-form-item label="操作" style="width:80%">
            <el-button
              type="primary"
              size="small"
              @click="submit"
              :disabled="btn.pushmsgVisable"
              >发布</el-button
            >
            <el-button
              type="primary"
              size="small"
              @click="dotask"
              :disabled="btn.sh_pushmsgDisabled"
              >定时发布</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        msg: null
      },
      timeDet: 0,
      open: false,
      timer: null,
      btn: {
        pushmsgDisabled: false,
        sh_pushmsgDisabled: false
      },
      user: this.$store.getters["user/getUser"]
    };
  },
  methods: {
    submit() {
      let that = this;
      clearInterval(this.timer);
      this.btn.pushmsgVisable = true;
      this.$api.game.sendSysMsg(this.requestParam).then(res => {
        if (res.data.success === true) {
          this.$message({
            showClose: true,
            message: "发布成功!",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: "发布失败",
            type: "error"
          });
        }
        that.btn.pushmsgVisable = false;
      });
    },
    dotask() {
      if (this.timeDet < 6) {
        this.$message({
          showClose: true,
          message: "间隔时间不能太短",
          type: "error"
        });
        return;
      }
      setInterval(() => {
        console.log("定时任务触发...");
        let that = this;
        this.$api.game.sendSysMsg(this.requestParam).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "发布成功!",
              type: "success"
            });
            that.btn.sh_pushmsgDisabled = true;
          } else {
            this.$message({
              showClose: true,
              message: "发布失败",
              type: "error"
            });
          }
        });
      }, this.timeDet * 1000);
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.inputstyle {
  width: 1000px;
}
</style>



